<template>
  <v-container fluid>
      <v-card class="pa-8">

    <v-row align="center" justify="center">
      <v-col>
        <v-select
        v-model="selectedCategory"
          :items="categories"
          label="Select category"
        ></v-select>
      </v-col>
    </v-row>

    <v-row>
      <v-col>
        <v-select
        v-model="selectedItems"     
          multiple
          :items="items"
          label="Select required items"
        ></v-select>
      </v-col>

    </v-row>
    <v-row>
        <v-col>
            <v-btn @click.prevent="saveItems">Save</v-btn>
        </v-col>
    </v-row>
    </v-card>
  </v-container>
</template>

<script>
import Axios from "axios";
export default {
  data() {
    return {
        selectedCategory: "",
        selectedItems: [],
        responseCategoryId: 0,
      categories: [
        "Surgical",
        "Electronic",
        "Fasion",
        "Cosmetics",
        "Art & Crafts",
        "Automobile",
        "Spices",
        "Food & Drinks",
        "Pharmaceutical",
        "Timber",
        "Households",
        "Sports",
        "Software and Related",
        "Health & Hygiene",
        "Real Estate",
        "Constructions",
      ],
      items: [
        "Masks",
        "Sanitizer",
        "Gloves",
        "Blades",
        "Isoprophyl alcohol",
        "Mobile phones",
        "Kitchenware",
        "Computer & Laptop",
        "Computer Hardware",
        "Outfits",
        "Clothing accessories",
        "Footwear",
        "Creams & Talcs",
        "Cleansing and Scrubs",
        "makeup",
        "Drawings",
        "Handcrafts",
        "Pain Items",
        "Vehicles",
        "Spare parts",
        "Accessories",
        "Spices",
        "Vegetables",
        "Fruits",
        "Snacks",
        "Frizzy",
        "Fresh Juice",
        "Bakery",
        "Other",
        "Tablets",
        "Syrups",
        "Furniture",
        "Crafts",
        "Constructional",
        "Cleaning material",
        "Utility",
        "Clothing",
        "Accessories",
        "Saas",
        "Appications",
        "Administration"
      ],
    };
  },
  methods: {
      saveItems(){
          Axios.post("/savecategory", {
              name: this.selectedCategory
          }).then((response) => {
              this.responseCategoryId = response.data.id
              for(let i = 0; i < this.selectedItems.length; i++){
              Axios.put("/savecategory/"+this.responseCategoryId, {
                  items: {
                      name: this.selectedItems[i]
                  }
              }).then((response) => {
          console.log(response)
          })
          .catch((error) => {
            console.log(error);
          });
          }
          console.log(response)
          })
          .catch((error) => {
            console.log(error);
          });
      },
  }
};
</script>

<style>
</style>


